'use client'

import { useState, useEffect, useRef, useCallback } from 'react'
import type { 
  PlayerSeasonStats, 
  PlayerPropPrediction, 
  GameOutcomePrediction,
  TeamStats,
  TeamInfo,
  PropType,
  UpcomingGame,
  GoogleSportsGame 
} from '@/types/ai-prediction'
import type { PlayerSuggestion } from '@/types/player'
import { PROP_TYPES } from '@/types/ai-prediction'
import { logQuery } from '@/utils/queryLogger'
import AskAIFollowUp from '@/components/AskAIFollowUp'
import AICautionBanner from '@/components/AICautionBanner'

type PredictionMode = 'player' | 'game'

export default function AIPredictionPage() {
  const [mode, setMode] = useState<PredictionMode>('player')

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h1 className="text-2xl font-bold text-foreground">AI Predictions</h1>
        <p className="text-muted-foreground mt-1">Data-driven predictions powered by NBA statistics and AI</p>
      </div>

      {/* AI Caution Banner */}
      <AICautionBanner />

      {/* Mode Toggle */}
      <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-2 inline-flex">
        <button
          onClick={() => setMode('player')}
          className={`px-6 py-2 rounded-md font-medium transition-colors ${
            mode === 'player'
              ? 'bg-blue-600 text-white'
              : 'text-muted-foreground hover:text-foreground'
          }`}
        >
          üèÄ Player Props
        </button>
        <button
          onClick={() => setMode('game')}
          className={`px-6 py-2 rounded-md font-medium transition-colors ${
            mode === 'game'
              ? 'bg-purple-600 text-white'
              : 'text-muted-foreground hover:text-foreground'
          }`}
        >
          üèÜ Game Outcomes
        </button>
      </div>

      {/* Content */}
      {mode === 'player' ? <PlayerPropPredictor /> : <GameOutcomePredictor />}
    </div>
  )
}

// ==================== PLAYER PROP PREDICTOR ====================
function PlayerPropPredictor() {
  const [searchQuery, setSearchQuery] = useState('')
  const [suggestions, setSuggestions] = useState<PlayerSuggestion[]>([])
  const [showSuggestions, setShowSuggestions] = useState(false)
  const [selectedPlayer, setSelectedPlayer] = useState<PlayerSuggestion | null>(null)
  const [playerStats, setPlayerStats] = useState<PlayerSeasonStats | null>(null)
  const [propType, setPropType] = useState<PropType>('points')
  const [threshold, setThreshold] = useState<string>('')
  const [overUnder, setOverUnder] = useState<'over' | 'under'>('over')
  const [thresholdError, setThresholdError] = useState<string | null>(null)
  const [prediction, setPrediction] = useState<PlayerPropPrediction | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [loadingStats, setLoadingStats] = useState(false)
  const [loadingSuggestions, setLoadingSuggestions] = useState(false)
  const [searchError, setSearchError] = useState<string | null>(null)
  
  const searchRef = useRef<HTMLDivElement>(null)
  const debounceTimer = useRef<NodeJS.Timeout | null>(null)

  // Debounced search
  useEffect(() => {
    if (debounceTimer.current) {
      clearTimeout(debounceTimer.current)
    }

    if (searchQuery.trim().length < 2) {
      setSuggestions([])
      setSearchError(null)
      setLoadingSuggestions(false)
      return
    }

    setLoadingSuggestions(true)
    setSearchError(null)

    debounceTimer.current = setTimeout(async () => {
      try {
        const response = await fetch(`/api/player-search?query=${encodeURIComponent(searchQuery)}`)
        const data = await response.json()
        
        if (!response.ok) {
          throw new Error(data.error || 'Failed to search players')
        }
        
        if (data.success && data.data) {
          setSuggestions(data.data.slice(0, 8))
          setShowSuggestions(true)
          setSearchError(null)
        } else {
          setSuggestions([])
          setSearchError('No players found')
        }
      } catch (err) {
        console.error('Player search error:', err)
        setSuggestions([])
        setSearchError(err instanceof Error ? err.message : 'Error searching players')
      } finally {
        setLoadingSuggestions(false)
      }
    }, 300)

    return () => {
      if (debounceTimer.current) {
        clearTimeout(debounceTimer.current)
      }
    }
  }, [searchQuery])

  // Click outside handler
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (searchRef.current && !searchRef.current.contains(event.target as Node)) {
        setShowSuggestions(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])

  const handlePlayerSelect = async (player: PlayerSuggestion) => {
    setSelectedPlayer(player)
    setSearchQuery(player.full_name)
    setShowSuggestions(false)
    setSuggestions([])
    setPrediction(null)
    setError(null)
    
    // Fetch player stats
    setLoadingStats(true)
    try {
      const response = await fetch(`/api/player-season-stats?player_id=${player.id}&season=2024-25`)
      const data = await response.json()
      if (data.success) {
        setPlayerStats(data)
      } else {
        setError(data.error || 'Failed to fetch player stats')
      }
    } catch (err) {
      setError('Error loading player stats')
    } finally {
      setLoadingStats(false)
    }
  }

  const validateThreshold = (value: string): boolean => {
    setThresholdError(null)
    
    if (!value || value.trim() === '') {
      setThresholdError('Please enter a threshold value')
      return false
    }
    
    const numValue = parseFloat(value)
    
    if (isNaN(numValue)) {
      setThresholdError('Please enter a valid number')
      return false
    }
    
    if (numValue < 0) {
      setThresholdError('Value cannot be negative')
      return false
    }
    
    // Validate reasonable ranges for different prop types
    if (propType === 'points' && numValue > 100) {
      setThresholdError('Points value seems too high')
      return false
    }
    
    if ((propType === 'rebounds' || propType === 'assists') && numValue > 50) {
      setThresholdError(`${propType} value seems too high`)
      return false
    }
    
    if ((propType === 'steals' || propType === 'blocks') && numValue > 20) {
      setThresholdError(`${propType} value seems too high`)
      return false
    }
    
    if (propType === 'minutes' && numValue > 48) {
      setThresholdError('Minutes cannot exceed 48')
      return false
    }
    
    return true
  }

  const handlePrediction = async () => {
    if (!selectedPlayer || !playerStats) return
    
    // Validate threshold before making prediction
    if (!validateThreshold(threshold)) {
      return
    }

    setLoading(true)
    setError(null)
    
    try {
      const response = await fetch('/api/ai-predict-player', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          playerName: selectedPlayer.full_name,
          playerStats: playerStats,
          propType: propType,
          threshold: parseFloat(threshold),
          overUnder: overUnder,
        }),
      })

      const data = await response.json()
      if (data.success) {
        setPrediction(data)
        
        // Log the query to history
        logQuery({
          query_type: 'AI Prediction - Player Prop',
          details: {
            timestamp: new Date().toISOString(),
            playerName: selectedPlayer.full_name,
            playerId: selectedPlayer.id,
            team: selectedPlayer.team_abbreviation || undefined,
            propType: propType,
            threshold: parseFloat(threshold),
            overUnder: overUnder,
            prediction: data.prediction,
            confidence: data.confidence,
            projectedValue: data.projectedValue,
            reasoning: data.reasoning,
            statsUsed: data.statsUsed
          }
        })
      } else {
        setError(data.error || 'Failed to generate prediction')
      }
    } catch (err) {
      setError('Error generating prediction')
    } finally {
      setLoading(false)
    }
  }

  const getInitials = (name: string) => {
    return name
      .split(' ')
      .map(n => n[0])
      .join('')
      .toUpperCase()
      .slice(0, 2)
  }

  return (
    <div className="space-y-6">
      {/* Player Search */}
      <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
        <h2 className="text-lg font-semibold text-foreground mb-4">Select Player</h2>
        <div ref={searchRef} className="relative">
          <div className="relative">
            <input
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onFocus={() => suggestions.length > 0 && setShowSuggestions(true)}
              placeholder="Search for a player..."
              className="w-full px-4 py-3 border border-border rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent pr-10"
            />
            {loadingSuggestions && (
              <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-blue-600"></div>
              </div>
            )}
          </div>
          
          {/* Search error */}
          {searchError && searchQuery.trim().length >= 2 && (
            <div className="absolute z-10 w-full mt-2 bg-destructive/10 border border-destructive/30 rounded-lg shadow-lg p-3">
              <p className="text-sm text-destructive">{searchError}</p>
            </div>
          )}
          
          {/* Suggestions dropdown */}
          {showSuggestions && suggestions.length > 0 && !searchError && (
            <div className="absolute z-10 w-full mt-2 bg-card/95 backdrop-blur-md border border-border/50 rounded-lg shadow-lg max-h-80 overflow-y-auto">
              {suggestions.map((player) => (
                <button
                  key={player.id}
                  onClick={() => handlePlayerSelect(player)}
                  className="w-full px-4 py-3 text-left hover:bg-primary/10 transition-colors flex items-center gap-3 border-b border-border/30 last:border-b-0"
                >
                  <img
                    src={`https://cdn.nba.com/headshots/nba/latest/260x190/${player.id}.png`}
                    alt={player.full_name}
                    className="w-12 h-12 rounded-full object-cover"
                    onError={(e) => {
                      const target = e.target as HTMLImageElement
                      target.style.display = 'none'
                      const parent = target.parentElement
                      if (parent) {
                        const fallback = document.createElement('div')
                        fallback.className = 'w-12 h-12 rounded-full bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center text-white font-bold text-sm'
                        fallback.textContent = getInitials(player.full_name)
                        parent.insertBefore(fallback, target)
                      }
                    }}
                  />
                  <div className="flex-1">
                    <p className="font-medium text-foreground">{player.full_name}</p>
                    <p className="text-sm text-muted-foreground">
                      {player.team_abbreviation || 'NBA'}
                      {player.is_active ? ' ‚Ä¢ Active' : ' ‚Ä¢ Inactive'}
                    </p>
                  </div>
                </button>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Player Stats Display */}
      {selectedPlayer && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <div className="flex items-center gap-4 mb-6">
            <img
              src={`https://cdn.nba.com/headshots/nba/latest/260x190/${selectedPlayer.id}.png`}
              alt={selectedPlayer.full_name}
              className="w-20 h-20 rounded-lg object-cover border-2 border-blue-200"
              onError={(e) => {
                const target = e.target as HTMLImageElement
                target.style.display = 'none'
                const parent = target.parentElement
                if (parent) {
                  const fallback = document.createElement('div')
                  fallback.className = 'w-20 h-20 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center text-white font-bold text-xl border-2 border-blue-200'
                  fallback.textContent = getInitials(selectedPlayer.full_name)
                  parent.insertBefore(fallback, target)
                }
              }}
            />
            <div>
              <h3 className="text-xl font-bold text-foreground">{selectedPlayer.full_name}</h3>
              <p className="text-muted-foreground">{selectedPlayer.team_abbreviation || 'NBA'}</p>
            </div>
          </div>

          {loadingStats ? (
            <div className="flex items-center justify-center py-12">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
            </div>
          ) : playerStats ? (
            <>
              {/* Season Averages */}
              <div className="mb-6">
                <h4 className="font-semibold text-foreground mb-3">2024-25 Season Averages</h4>
                <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
                  <div className="bg-primary/10 rounded-lg p-3">
                    <p className="text-sm text-muted-foreground">Points</p>
                    <p className="text-2xl font-bold text-blue-600">{playerStats.seasonAverage.points.toFixed(1)}</p>
                  </div>
                  <div className="bg-green-500/10 rounded-lg p-3">
                    <p className="text-sm text-muted-foreground">Rebounds</p>
                    <p className="text-2xl font-bold text-green-600">{playerStats.seasonAverage.rebounds.toFixed(1)}</p>
                  </div>
                  <div className="bg-purple-500/10 rounded-lg p-3">
                    <p className="text-sm text-muted-foreground">Assists</p>
                    <p className="text-2xl font-bold text-purple-600">{playerStats.seasonAverage.assists.toFixed(1)}</p>
                  </div>
                  <div className="bg-orange-500/10 rounded-lg p-3">
                    <p className="text-sm text-muted-foreground">Steals</p>
                    <p className="text-2xl font-bold text-orange-600">{playerStats.seasonAverage.steals.toFixed(1)}</p>
                  </div>
                  <div className="bg-red-500/10 rounded-lg p-3">
                    <p className="text-sm text-muted-foreground">Blocks</p>
                    <p className="text-2xl font-bold text-red-600">{playerStats.seasonAverage.blocks.toFixed(1)}</p>
                  </div>
                </div>
              </div>

              {/* Recent Games */}
              <div className="mb-6">
                <h4 className="font-semibold text-foreground mb-3">Last 10 Games</h4>
                <div className="overflow-x-auto">
                  <table className="w-full text-sm">
                    <thead className="bg-secondary/30">
                      <tr>
                        <th className="px-3 py-2 text-left text-xs font-medium text-muted-foreground uppercase">Date</th>
                        <th className="px-3 py-2 text-left text-xs font-medium text-muted-foreground uppercase">Opp</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">PTS</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">REB</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">AST</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">STL</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">BLK</th>
                        <th className="px-3 py-2 text-center text-xs font-medium text-muted-foreground uppercase">MIN</th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                      {playerStats.recentGames.slice(0, 10).map((game, index) => (
                        <tr key={index} className="hover:bg-secondary/30">
                          <td className="px-3 py-2 text-foreground">{game.date}</td>
                          <td className="px-3 py-2 text-foreground">{game.opponent}</td>
                          <td className="px-3 py-2 text-center font-medium text-foreground">{game.points}</td>
                          <td className="px-3 py-2 text-center text-foreground">{game.rebounds}</td>
                          <td className="px-3 py-2 text-center text-foreground">{game.assists}</td>
                          <td className="px-3 py-2 text-center text-foreground">{game.steals}</td>
                          <td className="px-3 py-2 text-center text-foreground">{game.blocks}</td>
                          <td className="px-3 py-2 text-center text-foreground">{game.minutes}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>

              {/* Prop Type Selection */}
              <div className="mb-6">
                <h4 className="font-semibold text-foreground mb-3">Select Prop Type</h4>
                <div className="grid grid-cols-2 md:grid-cols-6 gap-3">
                  {PROP_TYPES.map((prop) => (
                    <button
                      key={prop.value}
                      onClick={() => {
                        setPropType(prop.value)
                        setThresholdError(null)
                        setPrediction(null)
                      }}
                      className={`px-4 py-3 rounded-lg font-medium transition-colors ${
                        propType === prop.value
                          ? 'bg-blue-600 text-white'
                          : 'bg-secondary text-muted-foreground hover:bg-secondary'
                      }`}
                    >
                      {prop.label}
                    </button>
                  ))}
                </div>
              </div>

              {/* Threshold Input */}
              <div className="mb-6">
                <h4 className="font-semibold text-foreground mb-3">Set Threshold</h4>
                <div className="flex flex-col md:flex-row gap-4">
                  {/* Over/Under Toggle */}
                  <div className="flex-shrink-0">
                    <label className="block text-sm text-muted-foreground mb-2">Direction</label>
                    <div className="inline-flex rounded-lg border border-border overflow-hidden">
                      <button
                        type="button"
                        onClick={() => {
                          setOverUnder('over')
                          setPrediction(null)
                        }}
                        className={`px-6 py-3 font-medium transition-colors ${
                          overUnder === 'over'
                            ? 'bg-green-600 text-white'
                            : 'bg-white text-muted-foreground hover:bg-secondary/30'
                        }`}
                      >
                        Over
                      </button>
                      <button
                        type="button"
                        onClick={() => {
                          setOverUnder('under')
                          setPrediction(null)
                        }}
                        className={`px-6 py-3 font-medium transition-colors border-l border-border ${
                          overUnder === 'under'
                            ? 'bg-red-600 text-white'
                            : 'bg-white text-muted-foreground hover:bg-secondary/30'
                        }`}
                      >
                        Under
                      </button>
                    </div>
                  </div>

                  {/* Numeric Input */}
                  <div className="flex-1">
                    <label className="block text-sm text-muted-foreground mb-2">
                      {PROP_TYPES.find(p => p.value === propType)?.label} Value
                    </label>
                    <div className="relative">
                      <input
                        type="number"
                        value={threshold}
                        onChange={(e) => {
                          setThreshold(e.target.value)
                          setThresholdError(null)
                          setPrediction(null)
                        }}
                        onBlur={() => {
                          if (threshold) {
                            validateThreshold(threshold)
                          }
                        }}
                        placeholder={`Enter ${propType} threshold (e.g., ${propType === 'points' ? '25.5' : propType === 'rebounds' ? '10.5' : propType === 'assists' ? '8.5' : propType === 'minutes' ? '35' : '2.5'})`}
                        step="0.5"
                        min="0"
                        className={`w-full px-4 py-3 border rounded-lg focus:ring-2 focus:border-transparent ${
                          thresholdError
                            ? 'border-red-300 focus:ring-red-500'
                            : 'border-border focus:ring-blue-500'
                        }`}
                      />
                      {threshold && (
                        <div className="absolute right-3 top-1/2 transform -translate-y-1/2 text-sm font-medium text-muted-foreground">
                          {PROP_TYPES.find(p => p.value === propType)?.label}
                        </div>
                      )}
                    </div>
                    {thresholdError && (
                      <p className="mt-2 text-sm text-red-600 flex items-center gap-1">
                        <span>‚ö†Ô∏è</span>
                        {thresholdError}
                      </p>
                    )}
                    {threshold && !thresholdError && (
                      <p className="mt-2 text-sm text-muted-foreground">
                        Predicting: <span className="font-semibold">{selectedPlayer.full_name}</span> will score{' '}
                        <span className={`font-bold ${
                          overUnder === 'over' ? 'text-green-600' : 'text-red-600'
                        }`}>
                          {overUnder} {threshold}
                        </span>{' '}
                        {propType}
                      </p>
                    )}
                  </div>
                </div>
              </div>

              {/* Generate Prediction Button */}
              <button
                onClick={handlePrediction}
                disabled={loading || !threshold}
                className="w-full px-6 py-4 bg-gradient-to-r from-blue-600 to-blue-700 text-white font-semibold rounded-lg hover:from-blue-700 hover:to-blue-800 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? (
                  <span className="flex items-center justify-center gap-2">
                    <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                    Generating AI Prediction...
                  </span>
                ) : !threshold ? (
                  '‚ö†Ô∏è Enter Threshold Value to Continue'
                ) : (
                  `ü§ñ Generate AI Prediction: ${overUnder.charAt(0).toUpperCase() + overUnder.slice(1)} ${threshold}`
                )}
              </button>
            </>
          ) : null}
        </div>
      )}

      {/* Error Display */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <p className="text-red-800">‚ö†Ô∏è {error}</p>
        </div>
      )}

      {/* Prediction Result */}
      {prediction && (
        <div className="bg-gradient-to-br from-blue-50 to-purple-50 rounded-lg shadow-lg p-6 border-2 border-blue-200">
          <div className="flex items-center gap-2 mb-4">
            <span className="text-3xl">üéØ</span>
            <h3 className="text-2xl font-bold text-foreground">AI Prediction</h3>
          </div>

          <div className="space-y-4">
            {/* Prop Details */}
            {prediction.threshold && prediction.overUnder && (
              <div className="bg-white rounded-lg p-4 border-l-4 ${
                prediction.overUnder === 'over' ? 'border-green-500' : 'border-red-500'
              }">
                <p className="text-sm text-muted-foreground mb-1">Prop Bet</p>
                <p className="text-lg font-bold text-foreground">
                  {prediction.playerName} - {PROP_TYPES.find(p => p.value === prediction.propType)?.label}
                </p>
                <p className="text-2xl font-bold ${
                  prediction.overUnder === 'over' ? 'text-green-600' : 'text-red-600'
                } mt-1">
                  {prediction.overUnder.toUpperCase()} {prediction.threshold}
                </p>
              </div>
            )}
            
            {/* Prediction */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-1">AI Recommendation</p>
              <p className="text-2xl font-bold text-blue-600">{prediction.prediction}</p>
              {prediction.projectedValue && (
                <p className="text-sm text-muted-foreground mt-1">Projected Value: {prediction.projectedValue.toFixed(1)} {prediction.propType}</p>
              )}
            </div>

            {/* Confidence */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-2">Confidence Level</p>
              <div className="flex items-center gap-3">
                <div className="flex-1 bg-secondary rounded-full h-3">
                  <div
                    className={`h-3 rounded-full ${
                      prediction.confidence === 'High' ? 'bg-green-600' :
                      prediction.confidence === 'Medium' ? 'bg-yellow-600' :
                      'bg-orange-600'
                    }`}
                    style={{ 
                      width: prediction.confidence === 'High' ? '90%' : 
                             prediction.confidence === 'Medium' ? '65%' : '40%' 
                    }}
                  ></div>
                </div>
                <span className={`px-3 py-1 rounded-full text-sm font-semibold ${
                  prediction.confidence === 'High' ? 'bg-green-100 text-green-800' :
                  prediction.confidence === 'Medium' ? 'bg-yellow-100 text-yellow-800' :
                  'bg-orange-100 text-orange-800'
                }`}>
                  {prediction.confidence}
                </span>
              </div>
            </div>

            {/* Reasoning */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-2">Analysis</p>
              <p className="text-foreground">{prediction.reasoning}</p>
            </div>

            {/* Stats Used */}
            {prediction.statsUsed && prediction.statsUsed.length > 0 && (
              <div className="bg-white rounded-lg p-4">
                <p className="text-sm text-muted-foreground mb-2">Key Statistics</p>
                <ul className="space-y-1">
                  {prediction.statsUsed.map((stat, index) => (
                    <li key={index} className="text-sm text-muted-foreground">‚Ä¢ {stat}</li>
                  ))}
                </ul>
              </div>
            )}
          </div>

          {/* Ask AI Follow-up */}
          <div className="mt-6 border-t border-border/50 pt-4">
            <AskAIFollowUp
              context={{
                type: 'player-prop',
                originalPrediction: prediction,
                stats: playerStats
              }}
            />
          </div>
        </div>
      )}
    </div>
  )
}

// ==================== GAME OUTCOME PREDICTOR ====================
function GameOutcomePredictor() {
  // Input mode selection
  const [inputMode, setInputMode] = useState<'upcoming' | 'manual'>('upcoming')
  
  // Upcoming games search
  const [gameSearch, setGameSearch] = useState('')
  const [upcomingGames, setUpcomingGames] = useState<GoogleSportsGame[]>([])
  const [showGameSuggestions, setShowGameSuggestions] = useState(false)
  const [loadingGames, setLoadingGames] = useState(false)
  const [selectedGame, setSelectedGame] = useState<GoogleSportsGame | null>(null)
  const [dataSource, setDataSource] = useState<string>('')
  
  // Manual team selection
  const [team1Search, setTeam1Search] = useState('')
  const [team2Search, setTeam2Search] = useState('')
  const [allTeams, setAllTeams] = useState<TeamInfo[]>([])
  const [team1Suggestions, setTeam1Suggestions] = useState<TeamInfo[]>([])
  const [team2Suggestions, setTeam2Suggestions] = useState<TeamInfo[]>([])
  const [showTeam1Suggestions, setShowTeam1Suggestions] = useState(false)
  const [showTeam2Suggestions, setShowTeam2Suggestions] = useState(false)
  const [selectedTeam1, setSelectedTeam1] = useState<TeamInfo | null>(null)
  const [selectedTeam2, setSelectedTeam2] = useState<TeamInfo | null>(null)
  
  const [teamStats, setTeamStats] = useState<{ [key: number]: TeamStats }>({})
  const [prediction, setPrediction] = useState<GameOutcomePrediction | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [loadingTeams, setLoadingTeams] = useState(false)
  
  const gameSearchRef = useRef<HTMLDivElement>(null)
  const team1Ref = useRef<HTMLDivElement>(null)
  const team2Ref = useRef<HTMLDivElement>(null)
  const gameDebounceTimer = useRef<NodeJS.Timeout | null>(null)

  // Debounced search for upcoming games
  useEffect(() => {
    if (gameDebounceTimer.current) {
      clearTimeout(gameDebounceTimer.current)
    }

    if (gameSearch.trim().length < 2) {
      setUpcomingGames([])
      setLoadingGames(false)
      return
    }

    setLoadingGames(true)

    gameDebounceTimer.current = setTimeout(async () => {
      try {
        // Use Google API instead of nba_api
        const response = await fetch(`/api/google-upcoming-games?team=${encodeURIComponent(gameSearch)}`)
        const data = await response.json()
        
        if (data.success && data.games && data.games.length > 0) {
          setUpcomingGames(data.games)
          setShowGameSuggestions(true)
          setDataSource(data.source || 'google')
        } else {
          setUpcomingGames([])
        }
      } catch (err) {
        console.error('Error fetching upcoming games from Google:', err)
        setUpcomingGames([])
      } finally {
        setLoadingGames(false)
      }
    }, 300)

    return () => {
      if (gameDebounceTimer.current) {
        clearTimeout(gameDebounceTimer.current)
      }
    }
  }, [gameSearch])
  
  // Load teams on mount
  useEffect(() => {
    const loadTeams = async () => {
      setLoadingTeams(true)
      try {
        const [teamsResponse, statsResponse] = await Promise.all([
          fetch('/api/teams-list'),
          fetch('/api/team-stats?season=2024-25')
        ])
        
        const teamsData = await teamsResponse.json()
        const statsData = await statsResponse.json()
        
        if (teamsData.success) {
          setAllTeams(teamsData.teams)
        }
        
        if (statsData.success) {
          const statsMap: { [key: number]: TeamStats } = {}
          statsData.teams.forEach((team: TeamStats) => {
            statsMap[team.teamId] = team
          })
          setTeamStats(statsMap)
        }
      } catch (err) {
        console.error('Error loading teams:', err)
      } finally {
        setLoadingTeams(false)
      }
    }
    loadTeams()
  }, [])

  // Filter team 1 suggestions
  useEffect(() => {
    if (team1Search.trim().length < 2) {
      setTeam1Suggestions([])
      return
    }
    
    const filtered = allTeams.filter(team => 
      team.full_name.toLowerCase().includes(team1Search.toLowerCase()) ||
      team.abbreviation.toLowerCase().includes(team1Search.toLowerCase()) ||
      team.city.toLowerCase().includes(team1Search.toLowerCase())
    ).slice(0, 8)
    
    setTeam1Suggestions(filtered)
    setShowTeam1Suggestions(true)
  }, [team1Search, allTeams])

  // Filter team 2 suggestions
  useEffect(() => {
    if (team2Search.trim().length < 2) {
      setTeam2Suggestions([])
      return
    }
    
    const filtered = allTeams.filter(team => 
      team.full_name.toLowerCase().includes(team2Search.toLowerCase()) ||
      team.abbreviation.toLowerCase().includes(team2Search.toLowerCase()) ||
      team.city.toLowerCase().includes(team2Search.toLowerCase())
    ).slice(0, 8)
    
    setTeam2Suggestions(filtered)
    setShowTeam2Suggestions(true)
  }, [team2Search, allTeams])

  // Click outside handlers
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (gameSearchRef.current && !gameSearchRef.current.contains(event.target as Node)) {
        setShowGameSuggestions(false)
      }
      if (team1Ref.current && !team1Ref.current.contains(event.target as Node)) {
        setShowTeam1Suggestions(false)
      }
      if (team2Ref.current && !team2Ref.current.contains(event.target as Node)) {
        setShowTeam2Suggestions(false)
      }
    }
    document.addEventListener('mousedown', handleClickOutside)
    return () => document.removeEventListener('mousedown', handleClickOutside)
  }, [])
  
  const handleGameSelect = (game: GoogleSportsGame) => {
    setSelectedGame(game)
    setGameSearch(`${game.team1.name} vs ${game.team2.name}`)
    setShowGameSuggestions(false)
    
    // Convert Google game format to team format
    const team1: TeamInfo = {
      id: 0, // We'll fetch actual team data later
      full_name: game.team1.name,
      abbreviation: game.team1.name.split(' ').pop() || game.team1.name,
      nickname: game.team1.name,
      city: game.team1.name,
      logo_url: game.team1.logo_url
    }
    
    const team2: TeamInfo = {
      id: 0,
      full_name: game.team2.name,
      abbreviation: game.team2.name.split(' ').pop() || game.team2.name,
      nickname: game.team2.name,
      city: game.team2.name,
      logo_url: game.team2.logo_url
    }
    
    setSelectedTeam1(team1)
    setSelectedTeam2(team2)
    setPrediction(null)
    setError(null)
    
    // Try to match with actual team data for stats
    matchTeamsWithStats(team1, team2)
  }
  
  const matchTeamsWithStats = async (team1: TeamInfo, team2: TeamInfo) => {
    // Try to find matching teams in allTeams by name
    const match1 = allTeams.find(t => 
      t.full_name.toLowerCase().includes(team1.full_name.toLowerCase()) ||
      team1.full_name.toLowerCase().includes(t.full_name.toLowerCase())
    )
    
    const match2 = allTeams.find(t => 
      t.full_name.toLowerCase().includes(team2.full_name.toLowerCase()) ||
      team2.full_name.toLowerCase().includes(t.full_name.toLowerCase())
    )
    
    if (match1) setSelectedTeam1(match1)
    if (match2) setSelectedTeam2(match2)
  }

  const handleTeam1Select = (team: TeamInfo) => {
    setSelectedTeam1(team)
    setTeam1Search(team.full_name)
    setShowTeam1Suggestions(false)
    setPrediction(null)
    setError(null)
  }

  const handleTeam2Select = (team: TeamInfo) => {
    setSelectedTeam2(team)
    setTeam2Search(team.full_name)
    setShowTeam2Suggestions(false)
    setPrediction(null)
    setError(null)
  }

  const handlePrediction = async () => {
    if (!selectedTeam1 || !selectedTeam2) return
    
    const team1Stats = teamStats[selectedTeam1.id]
    const team2Stats = teamStats[selectedTeam2.id]
    
    if (!team1Stats || !team2Stats) {
      setError('Team statistics not available')
      return
    }

    setLoading(true)
    setError(null)
    
    try {
      // Include live score if available
      const liveScoreInfo = selectedGame ? {
        status: selectedGame.status,
        team1Score: selectedGame.team1.score,
        team2Score: selectedGame.team2.score,
        venue: selectedGame.venue
      } : undefined
      
      const response = await fetch('/api/ai-predict-game', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          team1: selectedTeam1.full_name,
          team2: selectedTeam2.full_name,
          team1Stats: team1Stats,
          team2Stats: team2Stats,
          gameDate: selectedGame ? `${selectedGame.date} ${selectedGame.time}` : undefined,
          liveScore: liveScoreInfo,
        }),
      })

      const data = await response.json()
      if (data.success) {
        setPrediction(data)
        
        // Log the query to history
        logQuery({
          query_type: 'AI Prediction - Game Outcome',
          details: {
            timestamp: new Date().toISOString(),
            team1: selectedTeam1.full_name,
            team2: selectedTeam2.full_name,
            winner: data.winner,
            scorePrediction: data.scorePrediction,
            winProbability: data.winProbability,
            confidence: data.confidence,
            reasoning: data.reasoning,
            keyFactors: data.keyFactors,
            gameDate: selectedGame ? `${selectedGame.date} ${selectedGame.time}` : undefined,
            liveScore: liveScoreInfo
          }
        })
      } else {
        setError(data.error || 'Failed to generate prediction')
      }
    } catch (err) {
      setError('Error generating prediction')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="space-y-6">
      {/* Input Mode Toggle */}
      <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
        <h2 className="text-lg font-semibold text-foreground mb-4">Select Input Method</h2>
        <div className="inline-flex rounded-lg border border-border overflow-hidden">
          <button
            type="button"
            onClick={() => {
              setInputMode('upcoming')
              setPrediction(null)
              setError(null)
            }}
            className={`px-6 py-3 font-medium transition-colors ${
              inputMode === 'upcoming'
                ? 'bg-purple-600 text-white'
                : 'bg-white text-muted-foreground hover:bg-secondary/30'
            }`}
          >
            üìÖ Search Upcoming Games
          </button>
          <button
            type="button"
            onClick={() => {
              setInputMode('manual')
              setPrediction(null)
              setError(null)
            }}
            className={`px-6 py-3 font-medium transition-colors border-l border-border ${
              inputMode === 'manual'
                ? 'bg-green-600 text-white'
                : 'bg-card text-muted-foreground hover:bg-secondary/30'
            }`}
          >
            ‚úèÔ∏è Manual Team Entry
          </button>
        </div>
      </div>

      {/* Upcoming Games Search */}
      {inputMode === 'upcoming' && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-purple-900">üèÄ Find Upcoming Game</h2>
            {dataSource && (
              <span className="text-xs px-2 py-1 bg-secondary text-muted-foreground rounded">
                Source: {dataSource === 'google_serpapi' ? 'Google API' : dataSource === 'sample_data' ? 'Demo' : dataSource}
              </span>
            )}
          </div>
          <div ref={gameSearchRef} className="relative">
            <div className="relative">
              <input
                type="text"
                value={gameSearch}
                onChange={(e) => setGameSearch(e.target.value)}
                onFocus={() => upcomingGames.length > 0 && setShowGameSuggestions(true)}
                placeholder="Search by team name (e.g., Lakers, Celtics)..."
                className="w-full px-4 py-3 border border-purple-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent pr-10"
              />
              {loadingGames && (
                <div className="absolute right-3 top-1/2 transform -translate-y-1/2">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-purple-600"></div>
                </div>
              )}
            </div>
            
            {/* Upcoming Games Suggestions */}
            {showGameSuggestions && upcomingGames.length > 0 && (
              <div className="absolute z-10 w-full mt-2 bg-card/95 backdrop-blur-md border border-border/50 rounded-lg shadow-lg max-h-96 overflow-y-auto">
                {upcomingGames.map((game) => (
                  <button
                    key={game.game_id}
                    onClick={() => handleGameSelect(game)}
                    className={`w-full px-4 py-4 text-left hover:bg-primary/10 transition-colors border-b border-border/30 last:border-b-0 ${
                      game.status === 'live' ? 'bg-destructive/10' : ''
                    }`}
                  >
                    <div className="flex items-center gap-4">
                      {/* Team 1 */}
                      <div className="flex items-center gap-2 flex-1">
                        <img
                          src={game.team1.logo_url}
                          alt={game.team1.name}
                          className="w-10 h-10 object-contain"
                          onError={(e) => {
                            const target = e.target as HTMLImageElement
                            target.style.display = 'none'
                          }}
                        />
                        <div>
                          <p className="font-medium text-foreground">{game.team1.name}</p>
                          {game.team1.score !== undefined && (
                            <p className="text-lg font-bold text-purple-600">{game.team1.score}</p>
                          )}
                        </div>
                      </div>
                      
                      {/* VS */}
                      <div className="text-center">
                        <span className="text-sm font-bold text-muted-foreground">VS</span>
                      </div>
                      
                      {/* Team 2 */}
                      <div className="flex items-center gap-2 flex-1 justify-end">
                        <div className="text-right">
                          <p className="font-medium text-foreground">{game.team2.name}</p>
                          {game.team2.score !== undefined && (
                            <p className="text-lg font-bold text-purple-600">{game.team2.score}</p>
                          )}
                        </div>
                        <img
                          src={game.team2.logo_url}
                          alt={game.team2.name}
                          className="w-10 h-10 object-contain"
                          onError={(e) => {
                            const target = e.target as HTMLImageElement
                            target.style.display = 'none'
                          }}
                        />
                      </div>
                    </div>
                    
                    {/* Game Info */}
                    <div className="mt-2 text-xs text-muted-foreground text-center">
                      {game.date} {game.time && `‚Ä¢ ${game.time}`}
                      {game.status === 'live' && <span className="ml-2 text-red-600 font-bold">‚óè LIVE</span>}
                      {game.status === 'final' && <span className="ml-2 text-muted-foreground font-bold">FINAL</span>}
                    </div>
                  </button>
                ))}
              </div>
            )}
            
            {gameSearch.trim().length >= 2 && upcomingGames.length === 0 && !loadingGames && (
              <div className="absolute z-10 w-full mt-2 bg-card/95 backdrop-blur-md border border-border/50 rounded-lg shadow-lg p-4">
                <p className="text-sm text-muted-foreground">No upcoming games found. Try searching for a different team.</p>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Manual Team Selection */}
      {inputMode === 'manual' && (
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Team 1 */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <h2 className="text-lg font-semibold text-purple-900 mb-4">Team 1</h2>
          <div ref={team1Ref} className="relative">
            <input
              type="text"
              value={team1Search}
              onChange={(e) => setTeam1Search(e.target.value)}
              onFocus={() => team1Suggestions.length > 0 && setShowTeam1Suggestions(true)}
              placeholder="Search for a team..."
              className="w-full px-4 py-3 border border-purple-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            />
            
            {showTeam1Suggestions && team1Suggestions.length > 0 && (
              <div className="absolute z-10 w-full mt-2 bg-card/95 backdrop-blur-md border border-border/50 rounded-lg shadow-lg max-h-80 overflow-y-auto">
                {team1Suggestions.map((team) => (
                  <button
                    key={team.id}
                    onClick={() => handleTeam1Select(team)}
                    className="w-full px-4 py-3 text-left hover:bg-primary/10 transition-colors border-b border-border/30 last:border-b-0"
                  >
                    <p className="font-medium text-foreground">{team.full_name}</p>
                    <p className="text-sm text-muted-foreground">{team.abbreviation}</p>
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Team 2 */}
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <h2 className="text-lg font-semibold text-green-900 mb-4">Team 2</h2>
          <div ref={team2Ref} className="relative">
            <input
              type="text"
              value={team2Search}
              onChange={(e) => setTeam2Search(e.target.value)}
              onFocus={() => team2Suggestions.length > 0 && setShowTeam2Suggestions(true)}
              placeholder="Search for a team..."
              className="w-full px-4 py-3 border border-green-300 rounded-lg focus:ring-2 focus:ring-green-500 focus:border-transparent"
            />
            
            {showTeam2Suggestions && team2Suggestions.length > 0 && (
              <div className="absolute z-10 w-full mt-2 bg-card/95 backdrop-blur-md border border-border/50 rounded-lg shadow-lg max-h-80 overflow-y-auto">
                {team2Suggestions.map((team) => (
                  <button
                    key={team.id}
                    onClick={() => handleTeam2Select(team)}
                    className="w-full px-4 py-3 text-left hover:bg-primary/10 transition-colors border-b border-border/30 last:border-b-0"
                  >
                    <p className="font-medium text-foreground">{team.full_name}</p>
                    <p className="text-sm text-muted-foreground">{team.abbreviation}</p>
                  </button>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
      )}

      {/* Selected Game Display (from upcoming games) */}
      {selectedGame && inputMode === 'upcoming' && (
        <div className={`bg-card/50 backdrop-blur-sm rounded-xl shadow-md p-6 border ${
          selectedGame.status === 'live' 
            ? 'border-destructive/50' 
            : selectedGame.status === 'final'
            ? 'border-border/50'
            : 'border-primary/50'
        }`}>
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold text-foreground">üéØ Selected Game</h2>
            {selectedGame.status === 'live' && (
              <span className="animate-pulse px-3 py-1 bg-red-600 text-white text-sm font-bold rounded">
                ‚óè LIVE
              </span>
            )}
            {selectedGame.status === 'final' && (
              <span className="px-3 py-1 bg-gray-600 text-white text-sm font-bold rounded">
                FINAL
              </span>
            )}
          </div>
          
          <div className="flex items-center justify-center gap-8">
            {/* Team 1 */}
            <div className="text-center flex-1">
              <img
                src={selectedGame.team1.logo_url}
                alt={selectedGame.team1.name}
                className="w-24 h-24 mx-auto object-contain mb-2"
                onError={(e) => {
                  const target = e.target as HTMLImageElement
                  target.style.display = 'none'
                }}
              />
              <p className="font-bold text-foreground text-lg">{selectedGame.team1.name}</p>
              {selectedGame.team1.score !== undefined && (
                <p className="text-3xl font-bold text-purple-600 mt-2">{selectedGame.team1.score}</p>
              )}
            </div>
            
            <div className="text-center">
              {selectedGame.status === 'live' ? (
                <div>
                  <span className="text-2xl font-bold text-red-600">VS</span>
                  <p className="text-xs text-red-600 mt-1 animate-pulse">LIVE NOW</p>
                </div>
              ) : selectedGame.status === 'final' ? (
                <div>
                  <span className="text-2xl font-bold text-muted-foreground">VS</span>
                  <p className="text-xs text-muted-foreground mt-1">FINAL</p>
                </div>
              ) : (
                <span className="text-3xl font-bold text-purple-600">VS</span>
              )}
            </div>
            
            {/* Team 2 */}
            <div className="text-center flex-1">
              <img
                src={selectedGame.team2.logo_url}
                alt={selectedGame.team2.name}
                className="w-24 h-24 mx-auto object-contain mb-2"
                onError={(e) => {
                  const target = e.target as HTMLImageElement
                  target.style.display = 'none'
                }}
              />
              <p className="font-bold text-foreground text-lg">{selectedGame.team2.name}</p>
              {selectedGame.team2.score !== undefined && (
                <p className="text-3xl font-bold text-purple-600 mt-2">{selectedGame.team2.score}</p>
              )}
            </div>
          </div>
          
          <div className="mt-6 grid grid-cols-1 md:grid-cols-3 gap-4 text-center">
            <div>
              <p className="text-xs text-muted-foreground">Date</p>
              <p className="text-sm font-semibold text-foreground">{selectedGame.date}</p>
            </div>
            <div>
              <p className="text-xs text-muted-foreground">Time</p>
              <p className="text-sm font-semibold text-foreground">{selectedGame.time || 'TBD'}</p>
            </div>
            {selectedGame.venue && (
              <div>
                <p className="text-xs text-muted-foreground">Venue</p>
                <p className="text-sm font-semibold text-foreground">{selectedGame.venue}</p>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Team Stats Comparison */}
      {selectedTeam1 && selectedTeam2 && teamStats[selectedTeam1.id] && teamStats[selectedTeam2.id] && (
        <div className="bg-card/50 backdrop-blur-sm rounded-xl shadow-md border border-border/50 p-6">
          <h2 className="text-lg font-semibold text-foreground mb-4">Team Statistics Comparison</h2>
          
          <div className="grid grid-cols-2 gap-6">
            {/* Team 1 Stats */}
            <div className="space-y-3">
              <div className="text-center mb-4">
                <h3 className="text-xl font-bold text-purple-900">{selectedTeam1.full_name}</h3>
                <p className="text-muted-foreground">{teamStats[selectedTeam1.id].wins}-{teamStats[selectedTeam1.id].losses}</p>
              </div>
              
              <div className="bg-purple-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">PPG</p>
                <p className="text-xl font-bold text-purple-600">{teamStats[selectedTeam1.id].pointsPerGame.toFixed(1)}</p>
              </div>
              <div className="bg-purple-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">Opp PPG</p>
                <p className="text-xl font-bold text-purple-600">{teamStats[selectedTeam1.id].pointsAllowed.toFixed(1)}</p>
              </div>
              <div className="bg-purple-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">FG%</p>
                <p className="text-xl font-bold text-purple-600">{teamStats[selectedTeam1.id].fieldGoalPct.toFixed(1)}%</p>
              </div>
              <div className="bg-purple-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">3P%</p>
                <p className="text-xl font-bold text-purple-600">{teamStats[selectedTeam1.id].threePointPct.toFixed(1)}%</p>
              </div>
            </div>

            {/* Team 2 Stats */}
            <div className="space-y-3">
              <div className="text-center mb-4">
                <h3 className="text-xl font-bold text-green-900">{selectedTeam2.full_name}</h3>
                <p className="text-muted-foreground">{teamStats[selectedTeam2.id].wins}-{teamStats[selectedTeam2.id].losses}</p>
              </div>
              
              <div className="bg-green-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">PPG</p>
                <p className="text-xl font-bold text-green-600">{teamStats[selectedTeam2.id].pointsPerGame.toFixed(1)}</p>
              </div>
              <div className="bg-green-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">Opp PPG</p>
                <p className="text-xl font-bold text-green-600">{teamStats[selectedTeam2.id].pointsAllowed.toFixed(1)}</p>
              </div>
              <div className="bg-green-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">FG%</p>
                <p className="text-xl font-bold text-green-600">{teamStats[selectedTeam2.id].fieldGoalPct.toFixed(1)}%</p>
              </div>
              <div className="bg-green-500/10 rounded-lg p-3">
                <p className="text-sm text-muted-foreground">3P%</p>
                <p className="text-xl font-bold text-green-600">{teamStats[selectedTeam2.id].threePointPct.toFixed(1)}%</p>
              </div>
            </div>
          </div>

          {/* Generate Prediction Button */}
          <button
            onClick={handlePrediction}
            disabled={loading}
            className="w-full mt-6 px-6 py-4 bg-gradient-to-r from-purple-600 to-green-600 text-white font-semibold rounded-lg hover:from-purple-700 hover:to-green-700 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {loading ? (
              <span className="flex items-center justify-center gap-2">
                <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white"></div>
                Generating AI Prediction...
              </span>
            ) : (
              'ü§ñ Generate AI Prediction'
            )}
          </button>
        </div>
      )}

      {/* Error Display */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <p className="text-red-800">‚ö†Ô∏è {error}</p>
        </div>
      )}

      {/* Prediction Result */}
      {prediction && (
        <div className="bg-gradient-to-br from-purple-50 to-green-50 rounded-lg shadow-lg p-6 border-2 border-purple-200">
          <div className="flex items-center gap-2 mb-4">
            <span className="text-3xl">üèÜ</span>
            <h3 className="text-2xl font-bold text-foreground">AI Prediction</h3>
          </div>

          <div className="space-y-4">
            {/* Winner */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-1">Predicted Winner</p>
              <p className="text-2xl font-bold text-purple-600">{prediction.winner}</p>
              {prediction.scorePrediction && (
                <p className="text-sm text-muted-foreground mt-1">Score: {prediction.scorePrediction}</p>
              )}
            </div>

            {/* Win Probability */}
            {prediction.winProbability && (
              <div className="bg-white rounded-lg p-4">
                <p className="text-sm text-muted-foreground mb-2">Win Probability</p>
                <div className="flex items-center gap-3">
                  <div className="flex-1 bg-secondary rounded-full h-3">
                    <div
                      className="h-3 rounded-full bg-gradient-to-r from-purple-600 to-green-600"
                      style={{ width: `${prediction.winProbability}%` }}
                    ></div>
                  </div>
                  <span className="text-lg font-bold text-foreground">{prediction.winProbability}%</span>
                </div>
              </div>
            )}

            {/* Confidence */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-2">Confidence Level</p>
              <span className={`inline-block px-4 py-2 rounded-full text-sm font-semibold ${
                prediction.confidence === 'High' ? 'bg-green-100 text-green-800' :
                prediction.confidence === 'Medium' ? 'bg-yellow-100 text-yellow-800' :
                'bg-orange-100 text-orange-800'
              }`}>
                {prediction.confidence}
              </span>
            </div>

            {/* Reasoning */}
            <div className="bg-white rounded-lg p-4">
              <p className="text-sm text-muted-foreground mb-2">Analysis</p>
              <p className="text-foreground">{prediction.reasoning}</p>
            </div>

            {/* Key Factors */}
            {prediction.keyFactors && prediction.keyFactors.length > 0 && (
              <div className="bg-white rounded-lg p-4">
                <p className="text-sm text-muted-foreground mb-2">Key Factors</p>
                <ul className="space-y-1">
                  {prediction.keyFactors.map((factor, index) => (
                    <li key={index} className="text-sm text-muted-foreground">‚Ä¢ {factor}</li>
                  ))}
                </ul>
              </div>
            )}
          </div>

          {/* Ask AI Follow-up */}
          <div className="mt-6 border-t border-border/50 pt-4">
            <AskAIFollowUp
              context={{
                type: 'game-outcome',
                originalPrediction: prediction,
                stats: {
                  team1: selectedTeam1 ? teamStats[selectedTeam1.id] : null,
                  team2: selectedTeam2 ? teamStats[selectedTeam2.id] : null
                }
              }}
            />
          </div>
        </div>
      )}
    </div>
  )
}
